@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap');

.main-div{
    /* position: relative; */
  text-align: center;
  color: white;
  height: 100vh;
  padding-left: 1rem;
  background-color: #333;
  /* background-image: url(https://images.unsplash.com/photo-1506318137071-a8e063b4bec0?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxleHBsb3JlLWZlZWR8MTN8fHxlbnwwfHx8fA%3D%3D&w=1000&q=80); */

}


.go{
  margin-top: 2rem;
}

code{
  color: #fff;
  font-size: bolder;
}
button{
  transition: 1s;
  float: center;
}
button:hover{
  transform: translateY(-10px);
}
p{
  color: #eee;
  font-size: 2rem;
}
.right{
  float: right;
  padding-top: 6.25%;
}
#wave {
  position: relative;
  height: 70px;
  width: 100vw;
  background: #e0efe3;
}
#wave:before {
  content: "";
  display: block;
  position: absolute;
  border-radius: 100% 50%;
  width: 340px;
  height: 80px;
  background-color: white;
  right: -5px;
  top: 40px;
}

.main-text{
    font-size: 9rem;
    /* padding-top: 10rem; */
    color: #00e5ff;
    font-family: 'Poppins', sans-serif;
    /* position: absolute; */
    /* background: -webkit-linear-gradient(#00ffea, #0091ff); */
    transition: 3s;


}
.main-text::after{
  transform: translateY(-10px);
}
.main-section{
  padding-top: 12.5%;
}
.o-image{
  height: 200px;
  margin:auto;
  /* float: right; */
  padding-top: 12.5%;
}
.hero-img{
  width: 600px;
}



span{
  /* background-color: black; */
}

@media (max-width: 950px){
  .right{
    float: center;
  }
  .hero-img{
    float: center;
    height: 200px;
    padding-top: 5rem;
  }
  .main-text{
    font-size: 7rem;
  }
  .img-div{
    position: relative;


  left: 50%;




  transform: translate(-50%, 0);
  }
}
@media (max-width: 700px){
  .main-text{
    font-size: 3rem;
  }
}