@tailwind base;
@tailwind components;
@tailwind utilities;
table{
    border: 5px solid black;
    /* display: block; */
    margin-left: auto;
    margin-right: auto;
}
.insight-not{
    background-color: black;
}
.message-insights{
    background-color: #333;
}
td{
    text-align: center;
    /* padding: 5px; */
    border: 2px solid black;
}

tr{
    /* padding-top: 10px; */
    border: 2px solid black;
}

.earthquake-table{
    height: 500px;
    overflow-y: scroll;
}

.message{
    font-size: 3rem;
    color: #333;
}

.center {
    margin: auto;
    width: 60%;
    padding: 10px;
    margin-bottom: 2rem;
    text-align: center;
}
li{
    list-style-type: none;
    position: relative;
}

li::before{
    content: '[]';
    position: absolute;
    left: -0.8em;
    font-size: 2rem;
}